//
// Variables
//


@avatar-bg-default: @global-color-gray-light;
@avatar-bg-member: #7fc0d3; // one-off color that may need to become variable

@avatar-size-base: 6rem;
@avatar-size-large: 7.2rem;
@avatar-size-small: 4rem;
@avatar-size-xlarge: 9.6rem;


//
// Component
//


// 1. Fix issue with alignment of inlined avatars
.avatar {
  background-position: center center;
  background-size: cover;
  border-radius: 50%;
  color: @global-color-white;
  display: inline-flex;
  height: @avatar-size-base;
  margin-bottom: 0;
  overflow: hidden;
  pointer-events: none;
  position: relative;
  width: @avatar-size-base;
  vertical-align: bottom; // 1

  text {
    fill: @global-color-white;
    text-transform: uppercase;
  }
}


//
// Avatar Modifiers (Type)
//


.avatar--default {
  background-color: @avatar-bg-default;

  circle {
    fill: @avatar-bg-default;
  }
}

.avatar--member {
  background-color: @avatar-bg-member;

  circle {
    fill: @avatar-bg-member;
  }
}


//
// Avatar Modifiers (Sizes)
//


.avatar--large {
  height: @avatar-size-large;
  width: @avatar-size-large;
}

.avatar--xlarge {
  height: @avatar-size-xlarge;
  width: @avatar-size-xlarge;
}

.avatar--small {
  height: @avatar-size-small;
  // use min-width to prevent flex shrinking since we know the width
  // otherwise we would use flex-shrink: 0;
  min-width: @avatar-size-small;
  width: @avatar-size-small;
}


//
// Avatar Icon
//


.avatar__icon {
  position: absolute;
  fill: currentColor;
  height: 80%;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0.75;
  bottom: -2.5%;
  width: 80%;

  .icon-stroke {
    stroke-width: none;
  }
}
